.wrapper {
    min-width: 30%;
    max-width: 350px;
    height: 100%;
    background-color: rgb(255 255 255 / 83%);
    overflow: auto;
    margin: 8px;
    height: initial;
    border-radius: 10px;
    position: relative;
}

::-webkit-scrollbar {
    width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey;
    border-radius: 10px;
    margin: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #858585;
    border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #6d6d6d;
}
